.card {
    background-color: lightcyan;
    border-radius: 6px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    margin-left: 100px;
    margin-right: auto;
    margin-top: 10px;
    margin-bottom: auto;
    min-width: 100px;
    max-width: 150px;
    min-height: 100px;
    max-height: 150px;
    flex: 1;
    cursor: pointer;
    /* width: 180px;
    margin-left: 60px;
    margin-right: 60px;
    margin-top:30px;
    margin-bottom:30px; */
}

.label {
    text-align: center;
    font-family: cursive;
    color: crimson;
    font-size: larger;
    font-weight: bolder;
    text-shadow: 1px 1px 1px crimson;
    padding: 5px;
}

.labelDiv {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.image {
    height: 94px;
    border-bottom: 3px solid skyblue;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}